<template>
  <v-container>
    <br />
    <v-row>
      <v-col class="text-center">
        <v-btn href="#" text>
          <h4 class="mr-2">Archive</h4>
        </v-btn>

        <v-btn href="#" text>
          <h4 class="mr-2">Delete</h4>
        </v-btn>
      </v-col>
    </v-row>
    <v-row>
      <v-col cols="5"></v-col>
      <v-col><TaskList :tasks="t" /></v-col>
      <v-col></v-col>
    </v-row>
  </v-container>
</template>

<script>
// import TitleBar from "../components/TitleBar.vue";
import TaskList from "../components/TaskList.vue";
import axios from "axios";
// import { delete } from 'vue/types/umd';
var webcall = axios.create({
  baseURL: "https://api.thecatapi.com/v1/images",
  timeout: 20000,
  withCredentials: false,
  headers: { "Content-Type": "application/json" },
});

export default {
  name: "App",

  components: {
    // TitleBar,
    TaskList,
  },

  data: () => ({
    t: [],
  }),
  methods: {
    catchCat() {
      var vm = this;
      var url = "/search";
      for (let i = 0; i < 10; i++) {
        try {
          webcall.get(url).then(async function (response) {
            var temp = await JSON.parse(JSON.stringify(response.data))[0]
            vm.t.push(temp["url"]);
            console.log(temp);
          });
        } catch (err) {
          console.log("error");
          alert(err);
        }
      }
    },
  },
  mounted() {//when the page is loading, do following
    this.catchCat();
  },
};
</script>
